<template>
  <div>
    <main class="container">
      <div class="shadow rounded alert alert-light">
        <div class="row">
          <div class="col-md-8">
            <div class="col-10" style="padding: 0px">
              <h3 class="h3">
                <b> </b>
              </h3>
              <span>
                <a @click="goPage('fl')" href="#"> </a>
                <a @click="loadProf($store.getters.get_user_info.user.id)" href="#">/ </a>
                <span class="text-muted">/  </span>
              </span>
            </div>
          </div>
          <div class="col-md-4 mt-1 pr-1">
            <button
              @click="goPage('addTask')"
              data-turbolinks="false"
              type="button"
              class="shadow btn btn-warning btn-lg btn-block"
            > </button>
          </div>
        </div>
      </div>
      <div class="row">
        <div v-if="tasks && tasks.length>0" class="col-md-8">
          <div class="alert alert-light shadow" v-for="task in displayedTasks">
            <h3>{{task.title }}</h3>
            <p>{{task.description| truncate(320, '...')}}</p>
            <div class="row">
              <div class="col-10" style="padding: 0px">
                <div class="mt-2">
                  <p>
                    <a
                      class="category"
                      @click="loadTasksByCategory(task.category)"
                      href="#"
                    >{{task.category.name}}</a>
                  </p>
                  <span class="text-muted">
                    <b class="user_image">:</b>
                    {{task.createdDate| filterDateTime}}
                  </span>
                  <p>
                    <b class="user_image">:</b>
                    <a @click="loadProf(task.author.id)" href="#">{{task.author.username}}</a>
                  </p>
                </div>
              </div>
              <div v-if="task.price!=0" class="col-2" style="padding: 0px">
                <p class="price">
                  <b>${{task.price}}</b>
                </p>
              </div>
            </div>
            <a href @click="loadTask(task.id)" class="btn btn-outline-warning"> </a>
          </div>
          <div id="pag" class="clearfix btn-group">
            <button
              type="button"
              class="btn btn-warning"
              v-if="page != 1"
              @click="page--"
            ></button>
            <button
              type="button"
              class="btn btn-outline-warning"
              v-for="pageNumber in pages.slice(page-1, page+5)"
              @click="page = pageNumber"
            >{{pageNumber}}</button>
            <button
              type="button"
              @click="page++"
              v-if="page < pages.length"
              class="btn btn-warning"
            ></button>
          </div>
        </div>
        <div v-if="tasks.length<1" class="col-md-8">
          <div class="shadow-lg alert alert-light">
            <h2>  ?</h2>
            <p>         :</p>
            <b>&bull;  </b>
            <p>     .</p>
            <p>    :</p>
            <b>-   </b>
            <p>
                   ,        .
                              .
            </p>
            <b>- </b>
            <p>          .</p>
            <b>- </b>
            <p>
                       .
                     ,    .
            </p>
            <b>&bull;    </b>
            <p>
              ,    ,       .
                ,            .
              <br />      ,   .
                         .
            </p>
            <b>&bull;   </b>
            <p>
                       !
                          .
            </p>
            <b>&bull;    </b>
            <p>
                       .
              <br />       ,         .
              <br />        ,   ,      .
              <br />        .
            </p>
          </div>
        </div>
        <aside class="col-md-4">
          <div id="multi-derevo" class="p-3 rounded nb-3 alert alert-light shadow">
            <h3 style="cursor: pointer">
              <b @click="clearTasks()"> </b>
            </h3>
            <p>
              <a class="a" href="#" @click="loadTasks()"> </a>
            </p>
            <p>
              <a class="a" href="#" @click="loadProf($store.getters.get_user_info.user.id)"></a>
            </p>
            <p>
              <a class="a" href="#" @click="goPage('balance')"></a>
            </p>
          </div>
        </aside>
      </div>
    </main>
  </div>
</template>

<script>
import { get, post } from "./../../Ajax/Http";

export default {
  created() {},
  data() {
    return {
      tasks: [],
      perPage: 5,
      pages: [],
      page: 1
    };
  },
  computed: {
    displayedTasks() {
      return this.paginate(this.tasks);
    }
  },
  methods: {
    goPage(item) {
      this.$router.push({ name: item });
    },
    loadProf(id) {
      if (this.$store.getters.get_user_info.user.id == id) {
        this.$router.push({ name: "profile", params: { Id: id } });
      } else {
        this.$router.push({ name: "publicProfile", params: { Id: id } });
      }
    },
    clearTasks(){
      this.tasks=[];
    },
    loadTasksByCategory(category) {
      $.ajax({
        url:
          this.$store.getters.get_url_server +
          "api/v1/myTasksByCategory/" +
          category.id +
          "/",
        type: "GET",
        success: response => {
          if (response) {
            this.tasks = response;

            this.checkedCategory = category;
          }
        }
      });
    },
    loadTask(id) {
      this.$router.push({ name: "task_detail", params: { taskId: id } });
    },
    loadTasks() {
      const url = this.$store.getters.get_url_server + "api/v1/my/tasks/";
      get(url, response => {
        this.tasks = response;
      });
    },
    paginate(tasks) {
      let page = this.page;
      let perPage = this.perPage;
      let from = page * perPage - perPage;
      let to = page * perPage;
      return tasks.slice(from, to);
    },
    setPages() {
      let numberOfPages = Math.ceil(this.tasks.length / this.perPage);
      this.pages = [];
      for (let index = 1; index <= numberOfPages; index++) {
        this.pages.push(index);
      }
    }
  },
  watch: {
    tasks() {
      this.setPages();
    }
  },
  filters: {
    //    
    filterDateTime(item) {
      let old_date = new Date(item);
      return `
                ${old_date.getDate()}.${old_date.getMonth() +
        1}.${old_date.getFullYear()}  ${old_date.getHours()}:${old_date.getMinutes()}`;
    }
  }
};
</script>