</template>


<script>
export default {
  name: "EditPost",
  created() {
    this.loadPost();
  },
  data() {
    return {
      title: "",
      text: "",
      mess: ""
    };
  },
  methods: {
    goPage(item) {
      this.$router.push({ name: item });
    },
    loadPost() {
      $.ajax({
        url:
          this.$store.getters.get_url_server +
          "api/v1/posts/" +
          this.$route.params.Id +
          "/",
        type: "GET",
        success: response => {
          (this.title = response.title), (this.text = response.text);
        }
      });
    },
    updatePost() {
      $.ajax({
        url:
          this.$store.getters.get_url_server +
          "api/v1/posts/" +
          this.$route.params.Id +
          "/",
        type: "PUT",
        data: {
          pk: this.$route.params.Id,
          title: this.title,
          text: this.text,
          avtor: this.$store.getters.get_user_info.user
        },
        success: response => {
          this.$router.push({ name: "my_posts" });
        },
        error: response => {
          console.log("False");
          if (response.status === 400) {
            this.mess = ",   !";
          }
        }
      });
    },
    addTagB() {
      this.text += "<b> </b>";
    },
    addTagH() {
      this.text += "<h3> </h3>";
    },
    addTagI() {
      this.text += "<i> </i>";
    },
    addTagU() {
      this.text += "<u> </u>";
    },
    addTagS() {
      this.text += "<s> </s>";
    },
    addTagImg() {
      this.text += '<img src= " http:// " class="img-thumbnail"/>';
    },
    addTagA() {
      this.text += '<a href="http:// "></a>';
    },
    addTagO() {
      this.text += "<ol>\n    <li> </li> \n</ol>";
    }
  }
};
</script>


<style>
.b {
  color: #ff0000;
}
.ul {
  padding: 1px;
}
.btnOtst {
  width: 30px;
  margin-left: 5px;
}
</style>
