            <h2>     "{{postid.title}}"</h2>
            <form method="POST">
              <button
                type="button"
                @click="deletePost()"
                class="btn btn-outline-danger"
                name="button"
              ></button>
              <a href="#" @click="goPage('my_posts')">, </a>
            </form>
          </div>
        </div>
        <aside class="col-md-4">
          <div class="p-3 rounded nb-3">
            <h3>
              <b>HTML,   </b>
            </h3>
            <p>
                  30    ,  -    HTML.       ,     ,
                 -  .       ,  ,   ,
               , ,    -.  - ,          ?
              -        ?  .HTML 1.0,   1993 ,   13 
              (  ,     ):
              <br />
              <i>
                <b>a, address, base, dd, dir, dl, dt, h1..h6, li, p, plaintext, title, ul</b>
              </i>
            </p>
            <a href="https://habr.com/ru/post/440336/" title="  habr" target="_blank">
              <button class="btn btn-warning"></button>
            </a>
            <p></p>
            <h3>
              <b>    1,6   </b>
            </h3>
            <div class="p-3 mb-3">
              <img
                src="https://habrastorage.org/webt/q8/s-/cq/q8s-cqlxfrv8abkdotnudzeq1u8.jpeg"
                class="img-thumbnail"
              />
