</template>

<script>
export default {
  name: "PostDetail",

  // props: {
  //   post: ""
  // },
  data() {
    return {
      postid: ""
    };
  },
  created() {
    this.loadPost();
  },
  beforeCreate() {
    $.ajax({
      url:
        this.$store.getters.get_url_server +
        "api/v1/posts/" +
        this.$route.params.Id +
        "/",
      data: {
        pk: this.$route.params.Id
      },
      type: "GET",
      success: response => {
        this.postid = response;
      }
    });
  },
  computed: {
    auth() {
      if (this.$store.getters.get_auth) return true;
      else return false;
    }
  },
  methods: {
    goPage(item) {
      this.$router.push({ name: item });
    },

    like(id) {
      $.ajax({
        url: this.$store.getters.get_url_server + "api/v1/like/",
        type: "POST",
        data: {
          pk: id
        },
        success: response => {
          this.loadPost();
        },
        error: response => {
          console.log("False");
        }
      });
    },
    loadPost() {
      $.ajax({
        url:
          this.$store.getters.get_url_server +
          "api/v1/posts/" +
          this.$route.params.Id +
          "/",
        type: "GET",
        success: response => {
          this.postid = response;
        }
      });
    },
    deletePost(id) {
      // if (this.$store.getters.get_user_info.user.username == this.postid.avtor.username || this.$store.getters.get_user_info.user.username == ""){
      $.ajax({
        url:
          this.$store.getters.get_url_server +
          "api/v1/posts/" +
          this.$route.params.Id +
          "/",
        type: "DELETE",
        data: {
          pk: id
        },
        success: response => {
          window.location = "/my";
        }
      });
      // }
    }
  },
  filters: {
    //    
    filterDateTime(item) {
      let old_date = new Date(item);
      return `
                 ${old_date.getDate()}.${old_date.getMonth()+1}.${old_date.getFullYear()}  ${old_date.getHours()}:${old_date.getMinutes()}`;
    }
  }
};
</script>


